body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.header__link {
  align-items: center;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__element {
  margin-left: 20px;
}

.nav__element:first-child {
  margin-left: 0;
}

.nav__element:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: #00acdc;
}
